<template>
	<view class="isMessage" :class="isSelf ? 'isNotSelf' : 'isSelf'" >
		<view :class="isSelf ? 'margin-right-sm' : 'margin-left-sm'">
			<u-avatar :size="80" :src="item.avatar" @click.stop="enterUserHome(item.from)"></u-avatar>
		</view>
		<view class="messageBlock flex align-center justify-center">
			<!-- 文字模式 -->
			<text class="text-justify" v-if="item.payload.text">{{ item.payload.text }}</text>
			<!-- 图片模式 -->
			<image :src="item.payload.imageInfoArray[2].url" mode="aspectFill" 
				@click="imgPrview(item.payload.imageInfoArray[2].url)"
				v-if="item.payload.imageInfoArray[2]" :style="imgStyle">
			</image>
		</view>
	</view>
</template>

<script>
export default {
	name: 'chatCard',
	data() {
		return {
			
		}
	},
	props: {
		isSelf: {
			type: Boolean,
			default() {
				return false;
			}
		},
		item: {
			type: Object,
			default() {
				return {};
			}
		}
	},
	methods: {
		// 进入用户主页
		enterUserHome(id) {
			this.$api.routerHandle.goto(`/pagesHome/mynews?id=${id}`);
		},
		// 图片预览
		imgPrview(url) {
			this.$api.imgHandle.imgPreview(url, [url]);
		},
	},
	computed: {
		imgStyle() {
			if (this.img) {
				return this.$api.imgHandle.multiImgShow([this.img])
			}
		},
		img() {
			if (this.item.payload && this.item.payload.imageInfoArray) {
				return this.item.payload.imageInfoArray[1];
			}
		}
	}
}
</script>

<style scoped lang="scss">
.isMessage {
	// 消息
	padding: 40rpx 20rpx;
	display: flex;
	display: flex;
	justify-content: flex-start;
}

.messageBlock {
	max-width: 450rpx;
	padding: 20rpx;
	// letter-spacing: 0.5rpx;
	min-width: 100rpx;
	border-radius: 10rpx;
}

.isNotSelf {
	// 自己
	.messageBlock {
		background-color: #0081ff;
		color: #fff;
	}
}
.isSelf {
	// 对方
	flex-direction: row-reverse;
	justify-self: flex-start;
	.messageBlock {
		background-color: #d9e4fa;
		color: #000;
	}
}
</style>
